.content {
  margin-left: 50%;
  padding: 20px;
  box-sizing: border-box;
  height: 100vh;
}

.content section {
  margin-bottom: 40px;
}

.content section h1 {
  margin-top: 0;
}

@media only screen and (max-width: 600px) {
  .content {
    margin: 0px;
    padding: 5px 10px;
  }
}
